import { useEffect, useState } from "react";
import EmojiData from "./emoji.json";
import "./styles.css";
function App() {
  const [search, setSearch] = useState("");
  const [data, setData] = useState([]);
  useEffect(() => {
    const newData = EmojiData.filter((emoji) =>
      emoji.name.toLowerCase().includes(search.toLowerCase())
    );
    setData(newData);
    console.log(search);
  }, [search]);
  return (
    <div className="App">
      <h1>Emoji Search</h1>
      <input
        type="text"
        placeholder="Search here"
        name="search"
        value={search}
        onChange={(e) => setSearch(e.target.value)}
      />
      {data.map((emoji) => (
        <div>
          <div className="card">
            <div
              className="card-body"
              onClick={() => {
                navigator.clipboard.writeText(emoji.char);
                alert("Emoji is copied!");
              }}
            >
              {emoji.char} {emoji.name}
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}
export default App;
